.friendMessage {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin:24px 0 23px;

}

.friendImageAndText {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    max-width: 70%;
    gap: 10px;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    display: flex;
    margin-top: 15px;
    border-radius: 50px;
}

.friendText {
    display: flex;
    flex-direction: column;
    background-color: #0059b3;
    max-width: 527px;
    height: 60px;
    align-items: flex-end;
    border-radius: 20px 20px 20px 3px;
}

.friendText:after {

}

.friendName {
    padding: 7px 352px 32px 27px;
    color: #FFF;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.friendMessageText {
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    padding: 32px 25px 8px 13px;
    color: #FFF;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.friendTime {
    color: #000;
    font-family: 'Montserrat', sans-serif;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    padding: 0px 11px 0 11px;
}
