.message {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin:24px 0 23px;

}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
    max-width: 70%;
    gap: 10px;
}

.imageAndText img {
    width: 48px;
    height: 48px;
    display: flex;
    margin-top: 15px;


}

.text {
    display: flex;
    flex-direction: column;
    background-color: #0059b3;
    max-width: 527px;
    height: 60px;
    align-items: flex-start;
    border-radius: 20px 20px 3px 20px;
}

.text:after {

}

.name {
padding: 7px 25px 31px 462px;
    color: #FFF;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.messageText {
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    padding: 32px 25px 8px 13px;
    color: #FFF;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.time {
    color: #000;
    font-family: 'Montserrat', sans-serif;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    padding: 1px 9px 0 10px;


}